export default "@import '~xtend-library/src/core/overlay/_overlay.less';\n\n/**\n * vars\n */\n\n// space\n\n@generate-overlay-space: @generate-container-space; // overlay external spacing\n\n// size\n\n@generate-overlay-size: tiny, small, medium, big, giant, full; // overlay internal spacing\n@overlay--tiny: @breakpoint-xs - 60;\n@overlay--small: @breakpoint-sm - 60;\n@overlay--medium: @breakpoint-md - 60;\n@overlay--big: @breakpoint-lg - 60;\n@overlay--giant: @breakpoint-xl - 60;\n@overlay--full: none;\n\n/**\n * overlay\n */\n\n.overlay {\n  .overlay-inner {\n    border-radius: 4px;\n  }\n  .overlay-design {\n    border-width: 1px;\n    border-style: solid;\n    border-color: transparent;\n  }\n  .btn-close {\n    .btn--giant();\n    .btn--icon();\n  }\n  // default on mobile\n  @media @max-sm {\n    .overlay-container {\n      padding: 0 !important;\n    }\n    .card--overlay {\n      .card--squared();\n    }\n  }\n  // default options\n  .overlay-size(small);\n}\n\n// special\n\n.overlay--disable {\n  // disable custom animation\n  .overlay-inner, .card--overlay > .card-inner > * {\n    opacity: 1 !important;\n    .trans-anim-none() !important;\n  }\n}\n\n// animation\n\n.overlay {\n  animation-duration: @time-medium + @time-small; // needed for .out animation timing, put max animation time\n  &.active {\n    .card--overlay > .card-design {\n      .anim-zoom.active();\n    }\n    .card--overlay > .card-inner > * {\n      .anim-y.active();\n      animation-delay: @time-small;\n    }\n    > .backdrop {\n      .backdrop.active();\n    }\n  }\n  &.out {\n    .card--overlay > .card-design {\n      .anim-zoom.out();\n      animation-delay: @time-small;\n    }\n    .card--overlay > .card-inner > * {\n      .anim-y.out();\n    }\n    > .backdrop {\n      .backdrop.out();\n    }\n  }\n}\n"