export default "/*\n * colors\n */\n\n@white: #fff;\n@black: #000;\n\n@gray-100: #f8fafe;\n@gray-200: #eff4fd;\n@gray-200--dark: darken(@gray-200, 5%);\n@gray-300: #d6e1f6;\n@gray-500: #7a7e8e;\n@gray-800: #373944;\n\n@accent: #7567f8;\n@accent--dark: darken(@accent, 5%);\n\n@text: @gray-800;\n@text--dark: @black;\n@text-inverse: fade(@white, 80%);\n@text-inverse--dark: @white;\n\n@success: #179922;\n@error: #bc1d1f;\n\n// shadow\n// USAGE: box-shadow: @shadow-light;\n\n@shadow-light: 0 .1rem 1rem fade(@gray-300, 40%), 0 .2rem 2rem fade(@gray-300, 80%);\n@shadow-dark: 0 .1rem 1rem fade(@gray-800, 20%), 0 .2rem 2rem fade(@gray-800, 40%);\n@shadow-accent: 0 .1rem 1rem fade(@accent--dark, 20%), 0 .2rem 2rem fade(@accent--dark, 40%);\n\n// gradient\n// USAGE: background: linear-gradient(135deg, @grad-light);\n\n@grad-light: @gray-100 33%, darken(@gray-100, 2%) 100%;\n@grad-dark: @gray-800 33%, darken(@gray-800, 2%) 100%;\n@grad-accent: @accent 33%, @accent--dark 100%;\n\n/**\n * spacing\n */\n\n@margins: auto auto, none 0, micro .25rem, tiny .5rem, small 1rem, medium 2rem, big 4rem, giant 6rem, huge 12rem;\n@paddings: auto auto, none 0, micro .25rem, tiny .5rem, small 1rem, medium 2rem, big 4rem, giant 6rem, huge 12rem;\n\n/**\n * order\n */\n\n@orders: -3, -2, -1, 0, 1, 2, 3;\n\n/**\n * z-index\n */\n\n@index-base: 0;\n@index-active: 2;\n@index-loader: 11;\n@index-sticky: 500; // @index-sticky by javascript 100 and decreses with sequential sticky\n@index-top: 501;\n@index-indicator: 510;\n@index-backdrop: 530;\n@index-drop: 550;\n@index-overlay: 600;\n@index-btn-close: 700;\n@index-last: 701;\n\n/**\n * breakpoints\n */\n\n@breakpoints: xs, sm, md, lg, xl; // if you change this change also variables names below\n@breakpoint-xs: 576px;\n@breakpoint-sm: 768px;\n@breakpoint-md: 992px;\n@breakpoint-lg: 1200px;\n@breakpoint-xl: 1500px;\n\n// @media queries\n// USAGE: @media @min-sm and @max-md {}\n\n@min-xs: ~'(min-width:'(@breakpoint-xs)~')';\n@max-xs: ~'(max-width:'(@breakpoint-xs - 1)~')';\n@min-sm: ~'(min-width:'(@breakpoint-sm)~')';\n@max-sm: ~'(max-width:'(@breakpoint-sm - 1)~')';\n@min-md: ~'(min-width:'(@breakpoint-md)~')';\n@max-md: ~'(max-width:'(@breakpoint-md - 1)~')';\n@min-lg: ~'(min-width:'(@breakpoint-lg)~')';\n@max-lg: ~'(max-width:'(@breakpoint-lg - 1)~')';\n@min-xl: ~'(min-width:'(@breakpoint-xl)~')';\n@max-xl: ~'(max-width:'(@breakpoint-xl - 1)~')';\n\n/**\n * background\n */\n\nbody {\n  background: @gray-200;\n}\n\n/**\n * scrollbar\n */\n\n.overflow-style(@mode: none) {\n  &::-webkit-scrollbar {\n    height: 8px;\n    width: 8px;\n    background: transparent;\n    @media (hover: none) {\n      display: none;\n    }\n  }\n  & when (@mode = none) {\n    &::-webkit-scrollbar-thumb {\n      background: fade(@gray-800, 30%);\n      &:hover {\n        background: fade(@gray-800, 60%);\n      }\n    }\n  }\n  & when (@mode = inner) {\n    &::-webkit-scrollbar-thumb {\n      background: fade(@gray-800, 20%);\n      &:hover {\n        background: fade(@gray-800, 40%);\n      }\n    }\n  }\n}\n\n/**\n * selection\n */\n\n::selection {\n  background-color: @accent--dark !important;\n  color: @white !important;\n}\n\n/**\n * focus\n * to disable focus add html.xt-focus-disable class\n */\n\nhtml.xt-focus:not(.xt-focus-disable) {\n  // custom focus\n  :focus {\n    outline-color: rgb(77, 144, 254);\n  }\n  // disable focus on forms\n  input, select, textarea {\n    &:focus {\n      outline: 0;\n    }\n  }\n  /*\n  // custom focus checkbox and radio\n  input[type=\"checkbox\"], input[type=\"radio\"] {\n    &:focus ~ .form-label:before {\n      outline-color: rgb(77, 144, 254);\n      outline-offset: -2px;\n      outline-style: auto;\n      outline-width: 5px;\n    }\n  }\n  */\n}\n\n/**\n * disabled\n */\n\n[disabled], .disabled {\n  cursor: default;\n  pointer-events: none;\n  // when not .active also graphic changes\n  &:not(.active) {\n    opacity: .75;\n  }\n}\n"