export default "/**\n * card\n */\n\n.card {\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  position: relative;\n  width: 100%;\n  .flex-auto();\n  z-index: @index-base; // fix :before negative z-index\n  a& {\n    .link-none() !important;\n  }\n  a&, button& {\n    &:hover, &.hover, &:active, &.active {\n      z-index: @index-active;\n    }\n  }\n  .card-contents({\n    margin: auto;\n  });\n}\n\n.card-inner {\n  .block();\n  > .card-content {\n    display: flex;\n    flex-direction: column;\n  }\n  // radius\n  &, & > * {\n    > *:first-child {\n      border-top-left-radius: inherit;\n      border-top-right-radius: inherit;\n    }\n    > *:last-child {\n      border-bottom-left-radius: inherit;\n      border-bottom-right-radius: inherit;\n    }\n  }\n  &, & > .card-content {\n    border-radius: inherit;\n  }\n}\n\n.card-design {\n  .design-setup();\n  border-radius: inherit;\n}\n\n.card-item {\n  .flex-full();\n  position: relative;\n  display: inline-block;\n}\n\n.card-asset {\n  .flex-full();\n  position: relative;\n  overflow: hidden;\n  > * {\n    width: 100%;\n    height: auto;\n  }\n}\n\n// selectors\n\n.card-contents(@rules) {\n  > .card-inner > .card-content, > .card-inner > .card-content > .card-item-nested {\n    @rules();\n  }\n}\n\n// special\n\n.card-group {\n  display: flex;\n}\n\n.card-item-nested {\n  margin: 0 !important;\n  padding: 0 !important;\n}\n\n.card--horizontal {\n  > .card-inner > .card-content {\n    flex-direction: row;\n    flex-wrap: nowrap;\n    align-items: stretch;\n    > *:first-child {\n      border-top-left-radius: inherit;\n      border-bottom-left-radius: inherit;\n      border-top-right-radius: initial;\n    }\n    > *:last-child {\n      border-top-right-radius: inherit;\n      border-bottom-right-radius: inherit;\n      border-bottom-left-radius: initial;\n    }\n  }\n  .card-asset {\n    > * {\n      width: 100%;\n      height: 100%;\n      object-fit: cover;\n    }\n  }\n}\n\n.card--disable {\n  .card--nodesign();\n  .card-block {\n    padding: 0 !important;\n  }\n}\n\n/**\n * @generate-card-group\n */\n\n// generate\n\n& when not (@generate-card-group = false) {\n\n  // generate-card--horizontal-responsive\n\n  .generate-card--horizontal-responsive();\n  .generate-card--horizontal-responsive(@i: length(@generate-card-group)) when (@i > 0) {\n    .generate-card--horizontal-responsive(@i - 1);\n    @breakpoint: extract(@generate-card-group, @i);\n    & when (@breakpoint = none) {\n      .card-group {\n        .card--group() !important;\n      }\n    }\n    & when not (@breakpoint = none) {\n      @query-min: 'min-@{breakpoint}';\n      @media @@query-min {\n        .card-group--@{breakpoint} {\n          .card-group() !important;\n        }\n      }\n    }\n  }\n\n}\n\n/**\n * @generate-box-horizontal\n */\n\n// generate\n\n& when not (@generate-box-horizontal = false) {\n\n  // generate-card--horizontal-responsive\n\n  .generate-card--horizontal-responsive();\n  .generate-card--horizontal-responsive(@i: length(@generate-box-horizontal)) when (@i > 0) {\n    .generate-card--horizontal-responsive(@i - 1);\n    @breakpoint: extract(@generate-box-horizontal, @i);\n    & when (@breakpoint = none) {\n      .card--horizontal {\n        .card--horizontal() !important;\n      }\n    }\n    & when not (@breakpoint = none) {\n      @query-min: 'min-@{breakpoint}';\n      @media @@query-min {\n        .card--horizontal--@{breakpoint} {\n          .card--horizontal() !important;\n        }\n      }\n    }\n  }\n\n}\n"