export default "@import '~xtend-library/src/core/note/_note.less';\n\n/**\n * note\n */\n\n.note {\n  .font-sans--semibold();\n  line-height: 1.4;\n  .note-title {\n    .font-sans--semibold();\n    line-height: 1.2;\n    letter-spacing: 0;\n    text-transform: none;\n  }\n  .note-list {\n    .list--default();\n  }\n  // default options\n  .note--medium();\n}\n\n// variant\n\n.note--default {\n  &:not(.note--background) {\n    color: @text;\n  }\n  &.note--background {\n    border-color: @gray-200;\n    background: @gray-200;\n    color: @text;\n  }\n}\n\n.note--primary {\n  &:not(.note--background) {\n    color: @accent;\n  }\n  &.note--background {\n    border-color: @accent;\n    background: @accent;\n    .text-inverse();\n  }\n  a {\n    color: inherit;\n  }\n}\n\n.note--success {\n  &:not(.note--background) {\n    color: @success;\n  }\n  &.note--background {\n    border-color: @success;\n    background: @success;\n    .text-inverse();\n  }\n  a {\n    color: inherit;\n  }\n}\n\n.note--error {\n  &:not(.note--background) {\n    color: @error;\n  }\n  &.note--background {\n    border-color: @error;\n    background: @error;\n    .text-inverse();\n  }\n  a {\n    color: inherit;\n  }\n}\n\n// size\n\n.note--small {\n  .margin(y, medium);\n  .margin(y, layout);\n  .font--small();\n  &.note--background {\n    padding: 1rem 1.5rem;\n  }\n  .note-list {\n    .list-space(small);\n  }\n  .note-title {\n    .spacing-tiny();\n    font-size: 1.7rem;\n  }\n  .note-icon {\n    font-size: 1.7rem;\n  }\n}\n\n.note--medium {\n  .margin(y, medium);\n  .margin(y, layout);\n  .font--medium();\n  &.note--background {\n    padding: 1.5rem 2rem;\n  }\n  .note-list {\n    .list-space(medium);\n  }\n  .note-title {\n    .spacing-small();\n    font-size: 2rem;\n  }\n  .note-icon {\n    font-size: 2rem;\n  }\n}\n\n.note--big {\n  .margin(y, medium);\n  .margin(y, layout);\n  .font--big();\n  &.note--background {\n    padding: 2rem 2.5rem;\n  }\n  .note-list {\n    .list-space(big);\n  }\n  .note-title {\n    .spacing-small();\n    font-size: 3rem;\n  }\n  .note-icon {\n    font-size: 3rem;\n  }\n}\n\n// special\n\n.note--background {\n  border-radius: .5em;\n  border-width: 1px;\n  border-style: solid;\n  border-color: transparent;\n}\n\n.note--squared {\n  border-radius: 0;\n}\n\n// animation\n\n.note {\n  &.active {\n    .anim-x.active();\n    &.inverse {\n      .anim-x-inverse.active();\n    }\n  }\n  &.out {\n    .anim-x.out();\n    &.inverse {\n      .anim-x-inverse.out();\n    }\n  }\n}\n"