export default "/**\n * card full\n */\n\n.card--full {\n  color: @text;\n  > .card-design {\n    border-color: transparent;\n    background: transparent;\n  }\n  > .card-inner > .card-content {\n    padding: 0 !important;\n  }\n}\n\n// size\n\n.card--small {\n  .card--full& {\n    > .card-design {\n      top: -1.5rem;\n      left: -2rem;\n      bottom: -1.5rem;\n      right: -2rem;\n    }\n  }\n}\n\n.card--medium {\n  .card--full& {\n    > .card-design {\n      top: -2rem;\n      left: -2.5rem;\n      bottom: -2rem;\n      right: -2.5rem;\n    }\n  }\n}\n\n.card--big {\n  .card--full& {\n    > .card-design {\n      top: -3rem;\n      left: -3.5rem;\n      bottom: -3rem;\n      right: -3.5rem;\n    }\n  }\n}\n"