export default "@import '~xtend-library/src/core/drop/_drop.less';\n\n/**\n * vars\n */\n\n// space\n\n@drop-space: 6px; // space between button and drop\n\n/**\n * drop\n */\n\n.drop {\n  > .drop-inner {\n    border-radius: 4px;\n    > .drop-content {\n      > .list, > .list-block {\n        .padding(y, 1.3rem); // vertical space\n        min-width: 18rem;\n      }\n    }\n    > .drop-design {\n      border-width: 1px;\n      border-style: solid;\n      border-color: transparent;\n    }\n  }\n  // default options\n  .drop--left();\n  .drop--bottom();\n}\n\n// variant\n\n.drop--default {\n  > .drop-inner {\n    color: @text;\n    > .drop-design {\n      border-color: @gray-300;\n      background: @white;\n      box-shadow: @shadow-light;\n    }\n  }\n  > .drop-inner > .drop-content > .list--drop {\n    .list-btns({ .btn--text() });\n  }\n}\n\n.drop--primary {\n  > .drop-inner {\n    .text-inverse();\n    > .drop-design {\n      border-color: @gray-800;\n      background: @gray-800;\n      box-shadow: @shadow-dark;\n    }\n  }\n  > .drop-inner > .drop-content > .list--drop {\n    .list-btns({ .btn--text() });\n  }\n}\n\n// special\n\n.drop--squared {\n  > .drop-inner {\n    border-radius: 0;\n  }\n}\n\n.drop--noborder {\n  > .drop-inner {\n    > .drop-design {\n      border: 0;\n    }\n  }\n}\n\n// special\n\n.drop--disable {\n  // disable custom animation\n  > .drop > .drop-inner {\n    > .drop-content {\n      opacity: 1 !important;\n      .trans-anim-none() !important;\n    }\n  }\n}\n\n.drop--overflow-y {\n  > .drop-inner > .drop-content {\n    overflow-y: auto;\n    max-height: 25rem;\n    .overflow-style(@mode: inner);\n  }\n}\n\n// animation\n\n.drop-container {\n  &, > .drop {\n    animation-duration: @time-medium + @time-small; // needed for .out animation timing, put max animation time\n  }\n  &.active {\n    > .drop > .drop-inner {\n      > .drop-design {\n        .anim-zoom.active();\n      }\n      > .drop-content {\n        .anim-y.active();\n        animation-delay: @time-small;\n      }\n    }\n    > .backdrop {\n      .backdrop.active();\n    }\n  }\n  &.out {\n    > .drop > .drop-inner {\n      > .drop-design {\n        .anim-zoom.out();\n        animation-delay: @time-small;\n      }\n      > .drop-content {\n        .anim-y.out();\n      }\n    }\n    > .backdrop {\n      .backdrop.out();\n    }\n  }\n}\n"