export default "@import '~xtend-library/src/core/button/_button.less';\n\n/**\n * button\n */\n\n.btn {\n  border-radius: .5em;\n  border-width: 1px;\n  border-style: solid;\n  border-color: transparent;\n  .font-sans--semibold();\n  line-height: 1.2;\n  letter-spacing: .025em;\n  text-transform: uppercase;\n  // default options\n  .btn--medium();\n}\n\n// variant\n\n.btn--default {\n  .link-none();\n  border-color: @gray-200;\n  background: @gray-200;\n  color: @text;\n  &:hover, &.hover, &:active, &.active {\n    border-color: @gray-200--dark;\n    background: @gray-200--dark;\n    color: @text--dark;\n  }\n  &:active, &.active {\n    box-shadow: @shadow-light;\n  }\n}\n\n.btn--primary {\n  .link-none();\n  border-color: @accent;\n  background: @accent;\n  color: @text-inverse;\n  &:hover, &.hover, &:active, &.active {\n    border-color: @accent--dark;\n    background: @accent--dark;\n    color: @text-inverse--dark;\n  }\n  &:active, &.active {\n    box-shadow: @shadow-accent;\n  }\n}\n\n.btn--text {\n  .link-none();\n  .btn--nodesign();\n  color: inherit;\n  &:hover, &.hover, &:active, &.active {\n    color: inherit;\n    opacity: .75;\n  }\n}\n\n.btn--link {\n  .btn--none();\n  .btn--nodesign();\n  // reset\n  font-family: inherit !important;\n  font-style: inherit !important;\n  font-weight: inherit !important;\n  letter-spacing: inherit !important;\n  text-transform: inherit !important;\n}\n\n// size\n\n.btn--tiny {\n  padding: .5em 1em;\n  font-size: 10px;\n}\n\n.btn--small {\n  padding: .6em 1.2em;\n  font-size: 1.2rem;\n}\n\n.btn--medium {\n  padding: .6em 1.2em;\n  font-size: 1.335rem; // @FIX odd rem with .035 added\n}\n\n.btn--big {\n  padding: .6em 1.2em;\n  font-size: 1.4rem;\n}\n\n.btn--giant {\n  padding: .6em 1.2em;\n  font-size: 1.535rem; // @FIX odd rem with .035 added\n}\n\n// special\n\n.btn--nodesign {\n  border-color: transparent;\n  background: transparent;\n  &:hover, &.hover, &:active, &.active {\n    border-color: transparent;\n    background: transparent;\n  }\n  &:active, &.active {\n    box-shadow: none;\n  }\n}\n\n.btn--squared {\n  border-radius: 0;\n}\n\n.btn--noborder {\n  border: 0;\n}\n\n.btn--tall {\n  padding-top: .9em;\n  padding-bottom: .9em;\n}\n\n.btn--short {\n  padding-top: .3em;\n  padding-bottom: .3em;\n}\n\n.btn--wide {\n  padding-left: 1.8em;\n  padding-right: 1.8em;\n}\n\n.btn--narrow {\n  padding-left: .6em;\n  padding-right: .6em;\n}\n\n.btn--none {\n  .btn--nodesign();\n  .btn--squared();\n  padding: 0;\n}\n\n.btn--none-x {\n  .btn--nodesign();\n  .btn--squared();\n  padding-left: 0;\n  padding-right: 0;\n}\n\n.btn--none-y {\n  .btn--nodesign();\n  .btn--squared();\n  padding-top: 0;\n  padding-bottom: 0;\n}\n\n.btn--icon {\n  line-height: 1;\n  padding: .8em .8em;\n}\n\n.btn--transform {\n  letter-spacing: 0;\n  text-transform: none;\n}\n\n// animation\n\n.btn {\n  transition-property: all;\n  transition-duration: @time-medium;\n  transition-timing-function: @bez-out;\n  &:hover, &.hover, &:active, &.active {\n    transition-property: all;\n    transition-duration: @time-medium;\n    transition-timing-function: @bez-in;\n  }\n}\n\n// custom\n\n.btn-close {\n  cursor: pointer;\n  position: absolute;\n  top: 0;\n  right: 0;\n  .btn--text();\n  .btn--icon();\n  .icon-xt(@content: @icon-xt-close, @size: @icon-medium);\n}\n"