export default "@import '~xtend-library/src/core/table/_table.less';\n\n/**\n * vars\n */\n\n// border\n\n@table-border--width: 1px;\n\n// generate class: [false], [true] non responsive only, [sm, md, lg] responsive @breakpoints\n\n@generate-table--scroll: sm, md;\n\n/**\n * tables\n */\n\n.table, .table--scroll {\n  .margin(bottom, layout) !important;\n  .margin(bottom, medium);\n}\n\n// .table\n\n.table {\n  tr {\n    border-bottom: @table-border--width solid @gray-200;\n  }\n  th {\n    .padding(all, small);\n    .font-sans--semibold();\n    font-size: 11px;\n    line-height: 1.4;\n    letter-spacing: .025em;\n    text-transform: uppercase;\n  }\n  td {\n    .padding(all, small);\n    font-size: 1.3rem;\n    line-height: 1.4;\n  }\n  // tfoot\n  tfoot {\n    .padding(y, big) !important;\n    background: @gray-200;\n    th {\n      .reset-text();\n    }\n    tr {\n      border: 0;\n      th, td {\n        .padding(y, tiny) !important;\n        .padding(x, medium) !important;\n        text-align: right;\n        vertical-align: top;\n      }\n      &:first-child {\n        th, td {\n          .padding(top, medium) !important;\n        }\n      }\n      &:last-child {\n        th, td {\n          .padding(bottom, medium) !important;\n        }\n      }\n    }\n  }\n}\n\n// .table--narrow\n\n.table--narrow {\n  th, td {\n    .padding(x, layout);\n    background: transparent;\n  }\n}\n\n// .table--small\n\n.table--small {\n  th {\n    .padding(all, small);\n  }\n  td {\n    .padding(all, small);\n    font-size: 1.2rem;\n  }\n}\n\n// .table--stripe\n\n.table--stripe {\n  tbody tr:nth-of-type(odd) {\n    background: @gray-100;\n  }\n  tbody tr:nth-of-type(even) {\n    background: transparent;\n  }\n}\n\n// .table--border\n\n.table--border {\n  border-radius: .5em;\n  border: 3px solid @gray-200;\n}\n\n// .table--responsive\n\n.table--responsive {\n  @media @max-sm {\n    border-top: @table-border--width solid @gray-200;\n    thead, th {\n      display: none;\n    }\n    tr {\n      .block();\n      .padding(y, medium);\n    }\n    td {\n      display: inline-flex;\n      flex-direction: column;\n      justify-content: center;\n      .padding(x, medium);\n      .padding(y, small);\n      text-align: center;\n      .list {\n        justify-content: center;\n      }\n    }\n    // data-th\n    td:before {\n      .form-label();\n      content: attr(data-th);\n      width: 100%;\n      justify-content: center;\n      text-align: center;\n    }\n  }\n}\n"