export default "@import '~xtend-library/src/core/javascript/_javascript.less';\n\n/**\n * vars\n */\n\n@backdrop--opacity: .1;\n\n/**\n * backdrop\n */\n\n.backdrop {\n  background: darken(@accent--dark, 50%);\n}\n\n// animation\n\n.backdrop {\n  opacity: 0;\n  &.active {\n    animation-name: anim-backdrop--in;\n    animation-duration: inherit;\n    animation-timing-function: @bez-in;\n    animation-fill-mode: forwards;\n  }\n  &.out {\n    animation-name: anim-backdrop--out;\n    animation-duration: inherit;\n    animation-timing-function: @bez-out;\n    animation-fill-mode: forwards;\n  }\n}\n\n@keyframes anim-backdrop--in {\n  0% {\n    opacity: 0;\n  }\n  100% {\n    opacity: @backdrop--opacity;\n  }\n}\n\n@keyframes anim-backdrop--out {\n  0% {\n    opacity: @backdrop--opacity;\n  }\n  100% {\n    opacity: 0;\n  }\n}\n"