export default "/**\n * structure\n */\n\n// indexes\n\n.gatsby_site_header {\n  position: relative;\n  z-index: 9;\n}\n\n.gatsby_site_main {\n  position: relative;\n  z-index: 8;\n}\n\n.gatsby_site_footer {\n  position: relative;\n  z-index: 7;\n}\n\n// prevent horizontal overflow\n\n.gatsby_site_wrapper {\n  overflow: hidden;\n}\n\n.gatsby_site_header, .gatsby_site_main, .gatsby_site_footer {\n  .block();\n}\n\n// sticky footer\n\n.gatsby_site_wrapper {\n  display: flex;\n  flex-wrap: wrap;\n  flex-direction: column;\n  min-height: 100vh;\n}\n\n.gatsby_site_main,\n.gatsby_site_main_inner,\n.gatsby_site_article,\n.gatsby_site_article_inner {\n  display: flex;\n  flex-direction: column;\n  .flex-auto();\n}\n\n/**\n * article\n */\n\n.gatsby_site_article {\n  background: @white;\n  box-shadow: @shadow-light;\n}\n\n.gatsby_site_article {\n  @media @min-md {\n    margin-left: @gatsby_site_article_sidebar_width;\n  }\n}\n\n.gatsby_site_article_hero {\n  position: relative;\n  .container-space();\n  .padding(y, giant);\n  border-bottom: 1px solid @gray-200;\n  background: @gray-100;\n  p {\n    .font--big();\n    color: @gray-500;\n  }\n}\n\n.gatsby_site_article_hero_content {\n  .badge {\n    margin-top: -1rem;\n    margin-left: 1.5rem;\n  }\n  h1 {\n    em {\n      .font-sans--regular();\n      color: @gray-300;\n    }\n  }\n}\n\n.gatsby_site_article_hero_links {\n  position: absolute;\n  bottom: 0;\n  left: 0;\n  right: 0;\n  background: linear-gradient(180deg, @grad-light);\n  border-bottom: 1px solid @gray-200;\n  .container-space();\n  .padding(y, small);\n  > .row {\n    .row-space(medium);\n    justify-content: space-between;\n  }\n  .btn {\n    .link-none();\n    .btn--text();\n    .btn--small();\n    .btn--none-x();\n  }\n}\n\n.gatsby_site_article_hero {\n  // animations\n  overflow: hidden;\n  .gatsby_site_article_hero_links {\n    .anim-opacity.out();\n    > .row > * {\n      opacity: 0;\n      transform: translateY(1.5rem);\n      transition-property: opacity, transform;\n      transition-duration: @time-medium;\n      transition-timing-function: @bez-in;\n      transition-delay: 150ms; // @start + @child * @increment\n      .nth-child(@child: 3, @start: 0ms, @increment: 50ms, @decrement: 0ms, {\n        @transition-delay();\n      });\n    }\n  }\n  &:hover {\n    .gatsby_site_article_hero_links {\n      .anim-opacity.active();\n      > .row > * {\n        opacity: 1;\n        transform: translateY(0);\n        transition-property: opacity, transform;\n        transition-duration: @time-medium;\n        transition-timing-function: @bez-in;\n      }\n    }\n  }\n}\n\n.gatsby_site_article_content {\n  .flex-auto();\n  .container-space();\n  .padding(y, giant);\n}\n\n.gatsby_site_article_foot {\n  border-top: 1px solid @gray-200;\n  .container-space();\n  .padding(y, big);\n}\n\n/**\n * listing\n */\n\n.gatsby_listing {\n  > .row {\n    .row-space(huge);\n  }\n}\n\n.gatsby_listing_group {\n  .col(12);\n}\n\n.gatsby_listing_title {\n  .h6();\n  .header--block();\n  .spacing-big();\n}\n\n.gatsby_listing_items {\n  > .row {\n    .row-space(giant);\n  }\n}\n\n.gatsby_listing_column {\n  .col(12);\n  .col(6, sm);\n  .col(4, xl);\n}\n\n"