export default "/**\n * demo\n */\n\nhtml.gatsby_iframe-container {\n  #gatsby_body-inner {\n    padding: 2rem;\n  }\n}\n\nhtml.gatsby_iframe-full {\n  body {\n    overflow: hidden !important;\n  }\n}\n\nhtml.gatsby_iframe-inside {\n  body {\n    .overflow-style(@mode: inner);\n    background: @white !important;\n  }\n}\n\n#gatsby_body-inner {\n  position: relative;\n}\n\n/**\n * svg\n */\n\nsvg {\n  .font-sans--semibold();\n  font-size: 1.8rem;\n  text-transform: uppercase;\n}\n\n/**\n * .gatsby_demo-cols\n */\n\n.gatsby_demo-cols {\n  [class^='col-'], [class*=' col-'], [class^='demo--col-'], [class*=' demo--col-'] {\n    &:before {\n      position: relative;\n      float: left;\n      width: 100%;\n      height: 22px;\n      min-height: 100%;\n      border: 1px solid darken(@gray-200, 5%);\n      background: @gray-200;\n      content: attr(data-index);\n      .font-sans--semibold();\n      font-size: 12px;\n      color: @accent;\n      line-height: 1.8;\n      text-align: center;\n    }\n  }\n}\n\n.gatsby_demo-cols-nested {\n  [class^='col-'], [class*=' col-'] {\n    .gatsby_demo-cols();\n  }\n}\n\n/**\n * .demo\n */\n\n@gatsby_demo-height: 35rem;\n\npre {\n  max-height: @gatsby_demo-height;\n}\n\n.gatsby_demo {\n  .margin(bottom, medium);\n  .margin(bottom, layout);\n  // animations\n  border: 3px solid @gray-200;\n  transition-property: border-color;\n  transition-duration: @time-medium;\n  transition-timing-function: @bez-out;\n  .gatsby_demo_tabs {\n    overflow: hidden;\n  }\n  .gatsby_demo_tabs_right {\n    > * {\n      opacity: 0;\n      transform: translateY(1.5rem);\n      transition-property: opacity, transform;\n      transition-duration: @time-medium;\n      transition-timing-function: @bez-in;\n      transition-delay: 0ms; // @start + @child * @increment\n      .nth-child(@child: 3, @start: 150ms, @increment: 0ms, @decrement: 50ms, {\n        @transition-delay();\n      });\n    }\n  }\n  &:hover {\n    border-color: @gray-300;\n    transition-property: border-color;\n    transition-duration: @time-medium;\n    transition-timing-function: @bez-in;\n    .gatsby_demo_tabs_right {\n      > * {\n        opacity: 1;\n        transform: translateY(0);\n        transition-property: opacity, transform;\n        transition-duration: @time-medium;\n        transition-timing-function: @bez-in;\n      }\n    }\n    .gatsby_demo_tabs, .gatsby_demo_code_tabs {\n      .btn {\n        &:active, &.active {\n          border-color: @gray-300 !important;\n        }\n      }\n    }\n  }\n  @media (hover: none) {\n    .gatsby_demo_tabs_right {\n      > * {\n        opacity: 1 !important;\n        .transform-none() !important;\n        .transition-none() !important;\n      }\n    }\n  }\n  .btn--prev-demo, .btn--next-demo {\n    display: none;\n  }\n  #toggle--open-full & {\n    border: 0 !important;\n    .btn--prev-demo, .btn--next-demo {\n      display: block;\n    }\n    @media @max-sm {\n      .btn--prev-demo, .btn--next-demo {\n        display: none;\n      }\n    }\n    .gatsby_demo_tabs_right {\n      > * {\n        opacity: 1 !important;\n        .transform-none() !important;\n        .transition-none() !important;\n      }\n    }\n  }\n  // code\n  .gatsby_demo_item {\n    position: relative;\n    .toggle-flex();\n    .gatsby_demo_item_wrapper {\n      position: relative;\n    }\n    .gatsby_demo_code {\n      .toggle-block();\n      #toggle--open-full & {\n        position: absolute;\n        z-index: 4;\n        width: 100%;\n        height: 100%;\n      }\n      .gatsby_demo_code_inner {\n        display: flex;\n        flex-flow: column;\n        height: 100%;\n      }\n      &.active {\n        z-index: 5;\n      }\n    }\n    &[data-iframe] {\n      &:not(.loaded) {\n        iframe {\n          visibility: hidden;\n        }\n      }\n      &.loaded {\n        .gatsby_demo_item_wrapper > .loader {\n          display: none;\n        }\n      }\n      iframe {\n        display: block;\n        width: 100%;\n        height: 0;\n        border: 0;\n        &:not(.gatsby_iframe-full) {\n          height: @gatsby_demo-height;\n        }\n      }\n    }\n    &:not(.populated) {\n      .gatsby_demo_code_body {\n        width: 100%;\n        overflow: hidden;\n      }\n    }\n    &.populated {\n      .gatsby_demo_code_body_item {\n        .toggle-block();\n      }\n    }\n  }\n  .hljs {\n    z-index: 1;\n    .padding(top, small) !important;\n  }\n  .gatsby_demo_code_body {\n    z-index: 1;\n    flex: 1;\n  }\n  // tabs\n  .gatsby_demo_tabs, .gatsby_demo_code_tabs, .gatsby_demo_code_body {\n    background: @gray-100;\n    border-bottom: 1px solid @gray-200;\n  }\n  .gatsby_demo_tabs, .gatsby_demo_code_tabs {\n    position: relative;\n    display: flex;\n    align-items: flex-start;\n    justify-content: space-between;\n    > * {\n      .list();\n      .list--default();\n      .list-space(micro);\n    }\n    .gatsby_demo_tabs_right {\n      flex-wrap: nowrap;\n    }\n    .padding(y, 7px);\n    .padding(x, small);\n    .btn {\n      border-width: 3px;\n      padding: .4em .75em;\n      font-size: 10px;\n      color: fade(@text, 50%);\n      &:hover, &.hover, &:active, &.active {\n        border-color: @gray-200;\n        background: @white;\n        color: @text;\n      }\n    }\n  }\n  .gatsby_demo_tabs_inner {\n    margin-top: -3px;\n  }\n  // text\n  .gatsby_demo_text {\n    .font--small();\n    .padding(y, small);\n    .padding(x, medium);\n    border-bottom: 1px solid @gray-200;\n    code {\n      background: @gray-200 !important;\n    }\n  }\n  // .gatsby_demo_source\n  .gatsby_demo_source {\n    .padding(all, medium);\n  }\n  .gatsby_demo_source {\n    &:not(.gatsby_demo_source--from) {\n      display: none;\n    }\n  }\n}\n\ntable {\n  border-color: @gray-200; // @FIX initial color\n}\n\n.gatbsy_table {\n  border-color: @gray-200;\n  transition-property: border-color;\n  transition-duration: @time-medium;\n  transition-timing-function: @bez-out;\n  &:hover, &.hover, &:active, &.active {\n    border-color: @gray-300;\n    transition-property: border-color;\n    transition-duration: @time-medium;\n    transition-timing-function: @bez-in;\n  }\n}\n\n// theme\n\n[data-gatsby-listing-toggle] + .gatsby_demo {\n  display: none;\n  .gatsby_demo_source_populate {\n    display: inline-flex; // populate source disable\n  }\n}\n\n.gatsby_demo, .gatsby_demo_item, .gatsby_demo_item_wrapper, .gatsby_demo_item_wrapper iframe {\n  display: flex;\n  flex-direction: column;\n  #toggle--open-full & {\n    .flex-full();\n  }\n}\n\n#toggle--open-full {\n  position: fixed;\n  z-index: @index-overlay;\n  top: 0;\n  left: 0;\n  bottom: 0;\n  right: 0;\n  background: @white;\n  @media @min-md {\n    margin-left: @gatsby_site_article_sidebar_width;\n  }\n  .btn-close {\n    .btn--primary();\n    .btn--squared();\n    font-size: 15px;\n  }\n  .gatsby_demo_tabs_right {\n    padding-right: 5rem;\n  }\n  .btn--open-full {\n    display: none !important;\n  }\n}\n\n#toggle--open-full-content {\n  display: flex;\n  min-height: 100vh;\n  pre {\n    max-height: none;\n  }\n}\n\n\nhtml.toggle--open-full--open {\n  body {\n    overflow-y: hidden !important;\n  }\n}\n"