export default "@import '~xtend-library/src/core/card/_card.less';\n\n/**\n * vars\n */\n\n// generate class: [false], [true] non responsive only, [sm, md, lg] responsive @breakpoints\n\n@generate-card-group: sm, md, lg;\n@generate-box-horizontal: sm, md, lg;\n\n/**\n * card\n */\n\n.card {\n  border-radius: .5em;\n  > .card-design {\n    border-width: 0;\n    border-style: solid;\n    border-color: transparent;\n  }\n  .card-title {\n    .spacing-small();\n    .font-sans--semibold();\n    line-height: 1.2;\n    letter-spacing: 0;\n    text-transform: none;\n  }\n  .card-contents({\n    > .card-item {\n      & + .card-item {\n        &:before {\n          content: '';\n          position: absolute;\n          top: 0;\n          left: 0;\n          right: 0;\n          height: 1px;\n        }\n      }\n    }\n  });\n  // default options\n  .card--medium();\n}\n\n// preset\n\n.card--overlay {\n  // default options\n  .card--big();\n  .card--white();\n}\n\n.card--slide {\n  // default options\n  .card--big();\n  .card--default();\n}\n\n.card--drop {\n  width: 40rem;\n  // default options\n  .card--small();\n  .card--white();\n}\n\n// variant\n\n.card--default {\n  color: @text;\n  > .card-design {\n    border-color: @gray-200;\n    background: @gray-200;\n  }\n  .card-contents({\n    > .card-item {\n      .card-item--default();\n    }\n  });\n  // preset\n  .card--overlay& {\n    > .card-design {\n      box-shadow: @shadow-light;\n    }\n  }\n  // interactive\n  a&, button& {\n    &:hover, &.hover, &:active, &.active {\n      color: @text--dark;\n      > .card-design {\n        border-color: @gray-200--dark;\n        background: @gray-200--dark;\n      }\n    }\n    &:active, &.active {\n      > .card-design {\n        box-shadow: @shadow-light;\n      }\n    }\n  }\n}\n\n.card-item--default {\n  & + .card-item {\n    &:before {\n      background: fade(@gray-800, 10%);\n    }\n  }\n}\n\n.card--primary {\n  .text-inverse();\n  > .card-design {\n    border-color: @accent;\n    background: @accent;\n  }\n  .card-contents({\n    > .card-item {\n      .card-item--primary();\n    }\n  });\n  // preset\n  .card--overlay& {\n    > .card-design {\n      box-shadow: @shadow-accent;\n    }\n  }\n  // interactive\n  a&, button& {\n    &:hover, &.hover, &:active, &.active {\n      color: @text-inverse--dark;\n      > .card-design {\n        border-color: @accent--dark;\n        background: @accent--dark;\n      }\n    }\n    &:active, &.active {\n      > .card-design {\n        box-shadow: @shadow-accent;\n      }\n    }\n  }\n}\n\n.card-item--primary {\n  & + .card-item {\n    &:before {\n      background: fade(@gray-200, 10%);\n    }\n  }\n}\n\n.card--white {\n  color: @text;\n  > .card-design {\n    border-color: @white;\n    background: @white;\n  }\n  .card-contents({\n    > .card-item {\n      .card-item--white();\n    }\n  });\n  // preset\n  .card--overlay& {\n    > .card-design {\n      box-shadow: @shadow-light;\n    }\n  }\n  // interactive\n  a&, button& {\n    &:hover, &.hover, &:active, &.active {\n      color: @text--dark;\n      > .card-design {\n        border-color: @white;\n        background: @white;\n      }\n    }\n    &:active, &.active {\n      > .card-design {\n        box-shadow: @shadow-light;\n      }\n    }\n  }\n}\n\n.card-item--white {\n  & + .card-item {\n    &:before {\n      background: fade(@gray-800, 10%);\n    }\n  }\n}\n\n// size\n\n.card-block.card-block--small {\n  .font--small();\n  padding: 1.5rem 2rem;\n  .card-title {\n    font-size: 1.7rem;\n  }\n}\n\n.card--small {\n  .card-block {\n    .card-block.card-block--small();\n  }\n}\n\n.card-block.card-block--medium {\n  .font--medium();\n  padding: 2rem 2.5rem;\n  .card-title {\n    font-size: 2rem;\n  }\n}\n\n.card--medium {\n  .card-block {\n    .card-block.card-block--medium();\n  }\n}\n\n.card-block.card-block--big {\n  .font--big();\n  padding: 3rem 3.5rem;\n  .card-title {\n    font-size: 3rem;\n  }\n}\n\n.card--big {\n  .card-block {\n    .card-block.card-block--big();\n  }\n}\n\n// special\n\n.card--nodesign {\n  > .card-design {\n    display: none;\n  }\n}\n\n.card--squared {\n  border-radius: 0;\n}\n\n.card--noborder {\n  > .card-design {\n    border: 0;\n  }\n}\n\n// special\n\n.card-group {\n  > .card-item {\n    & + .card-item {\n      &:before {\n        left: 0;\n        bottom: 0;\n        right: auto;\n        width: 1px;\n        height: auto;\n      }\n    }\n  }\n}\n\n.card--horizontal {\n  .card-contents({\n    > .card-item {\n      & + .card-item {\n        &:before {\n          left: 0;\n          bottom: 0;\n          right: auto;\n          width: 1px;\n          height: auto;\n        }\n      }\n    }\n  });\n}\n\n.card--overflow-y {\n  overflow-x: hidden;\n  overflow-y: auto;\n  max-height: 30rem;\n  .overflow-style(@mode: inner);\n}\n\n.card--disable {\n  color: @text !important;\n  &, .card-item {\n    opacity: 1 !important;\n    .transform-none() !important;\n    .trans-anim-none() !important;\n  }\n}\n\n// animation\n\n.card {\n  a&, button& {\n    transition-property: all;\n    transition-duration: @time-medium;\n    transition-timing-function: @bez-out;\n    > .card-design {\n      transition-property: all;\n      transition-duration: @time-medium;\n      transition-timing-function: @bez-out;\n    }\n    .card-block.card-item {\n      transition-property: transform;\n      transition-duration: @time-medium;\n      transition-timing-function: @bez-out;\n    }\n    &:hover, &.hover, &:active, &.active {\n      transform: translateY(-.5rem) scale(1.03, 1.05);\n      transition-property: all;\n      transition-duration: @time-medium;\n      transition-timing-function: @bez-in;\n      > .card-design {\n        transition-property: all;\n        transition-duration: @time-medium;\n        transition-timing-function: @bez-in;\n      }\n      .card-block.card-item {\n        transform: scale(0.97, 0.95);\n        transition-property: transform;\n        transition-duration: @time-medium;\n        transition-timing-function: @bez-in;\n      }\n    }\n  }\n}\n"