export default "@import '~xtend-library/src/extensions/overlay/position-setup.less';\n\n/**\n * overlay-position\n */\n\n.overlay-position {\n  .overlay-container {\n    .overlay-position--left& {\n      width: 88% !important;\n      max-width: 500px !important;\n    }\n    .overlay-position--right& {\n      width: 88% !important;\n      max-width: 500px !important;\n    }\n  }\n  .card--overlay {\n    .card--squared();\n    .card--noborder();\n  }\n}\n\n// animation\n\n.overlay-position {\n  animation-duration: @time-medium + @time-small; // needed for .out animation timing, put max animation time\n  &.active {\n    &.overlay-position--left {\n      .card--overlay > .card-design {\n        .anim-x.active();\n      }\n      .card--overlay > .card-inner > * {\n        .anim-x.active();\n        animation-delay: @time-small;\n      }\n    }\n    &.overlay-position--right {\n      .card--overlay > .card-design {\n        .anim-x-inverse.active();\n      }\n      .card--overlay > .card-inner > * {\n        .anim-x-inverse.active();\n        animation-delay: @time-small;\n      }\n    }\n  }\n  &.out {\n    &.overlay-position--left {\n      .card--overlay > .card-design {\n        .anim-x-inverse.out();\n        animation-delay: @time-small;\n      }\n      .card--overlay > .card-inner > * {\n        .anim-x-inverse.out();\n      }\n    }\n    &.overlay-position--right {\n      .card--overlay > .card-design {\n        .anim-x.out();\n        animation-delay: @time-small;\n      }\n      .card--overlay > .card-inner > * {\n        .anim-x.out();\n      }\n    }\n  }\n}\n"