export default "@import '~xtend-library/src/core/form/_form.less';\n\n/**\n * vars\n */\n\n// select\n\n@select-arrow-size: 4px; // in px\n@select-space-size: 24px; // in px\n\n/**\n * fieldset\n */\n\nfieldset + fieldset {\n  .margin(top, big);\n}\n\n/**\n * legend\n */\n\nlegend {\n  .block();\n  .h4();\n  .header--line();\n}\n\n/**\n * form\n */\n\nform, .form {\n  // default options\n  .form--default(); // .form-label .form-item\n  .form--medium(); // .form-group\n}\n\n/**\n * form-group\n */\n\n// size\n\n.form-group--small {\n  .margin(bottom, 1em);\n  font-size: 1.1rem;\n}\n\n.form--small {\n  .form-groups({ .form-group--small(); });\n}\n\n.form-group--medium {\n  .margin(bottom, 1.5em);\n  font-size: 1.3rem;\n}\n\n.form--medium {\n  .form-groups({ .form-group--medium(); });\n}\n\n.form-group--big {\n  .margin(bottom, 1.5em);\n  font-size: 1.5rem;\n}\n\n.form--big {\n  .form-groups({ .form-group--big(); });\n}\n\n// special\n\n.form-group--row {\n  @media @min-sm {\n    display: flex;\n    .row-space--x(medium);\n  }\n}\n\n.form-group--horizontal {\n  @media @min-sm {\n    display: flex;\n    .row-space--x(medium);\n    align-items: center;\n    text-align: right;\n    .form-label {\n      margin: 0;\n    }\n  }\n}\n\n.form-group--inline {\n  @media @min-sm {\n    display: flex;\n    flex-wrap: wrap;\n    .list-space--x(medium);\n    align-items: baseline;\n    .form-item {\n      width: auto;\n    }\n  }\n}\n\n/**\n * form-label\n */\n\n.form-label {\n  .margin(bottom, 1em); // size in em inherit .form-group size\n  .font-sans--semibold();\n  font-size: 1em; // size in em inherit .form-group size\n  line-height: 1.2;\n  letter-spacing: .025em;\n  text-transform: uppercase;\n}\n\n// variant\n\n.form-label--default {\n  color: @gray-500;\n}\n\n.form--default {\n  .form-labels({ .form-label--default(); });\n}\n\n.form-label--inverse {\n  color: @text-inverse;\n}\n\n.form--inverse {\n  .form-labels({ .form-label--inverse(); });\n}\n\n/**\n * form-item\n */\n\n.form-item {\n  border-radius: .5em;\n  border-width: 1px;\n  border-style: solid;\n  border-color: transparent;\n  padding: .75em 1.2em; // size in em inherit .form-group size\n  .font-sans--regular();\n  font-size: 1em; // size in em inherit .form-group size\n  line-height: 1.4;\n}\n\n// variant\n\n.form-item--default {\n  border-color: @gray-200;\n  background-color: @gray-200; // @FIX no background: or it overrides background-image\n  color: @text;\n  &:focus, &.active {\n    border-color: @gray-200--dark;\n    background-color: @gray-200--dark; // @FIX no background: or it overrides background-image\n  }\n  // placeholder\n  &:-ms-input-placeholder {\n    color: @gray-500;\n  }\n  &:-moz-placeholder {\n    color: @gray-500;\n  }\n  &::-webkit-input-placeholder {\n    color: @gray-500;\n  }\n  &::-ms-input-placeholder {\n    color: @gray-500;\n  }\n  &::-moz-placeholder {\n    color: @gray-500;\n  }\n  &::placeholder {\n    color: @gray-500;\n  }\n}\n\n.form--default {\n  .form-items({ .form-item--default(); });\n}\n\n.form-item--inverse {\n  border-color: fade(@gray-800, 20%);\n  background-color: fade(@gray-800, 20%); // @FIX no background: or it overrides background-image\n  color: @text-inverse--dark;\n  &:focus, &.active {\n    border-color: fade(@gray-800, 40%);\n    background-color: fade(@gray-800, 40%); // @FIX no background: or it overrides background-image\n  }\n  // placeholder\n  &:-ms-input-placeholder {\n    color: @text-inverse;\n  }\n  &:-moz-placeholder {\n    color: @text-inverse;\n  }\n  &::-webkit-input-placeholder {\n    color: @text-inverse;\n  }\n  &::-ms-input-placeholder {\n    color: @text-inverse;\n  }\n  &::-moz-placeholder {\n    color: @text-inverse;\n  }\n  &::placeholder {\n    color: @text-inverse;\n  }\n}\n\n.form--inverse {\n  .form-items({ .form-item--inverse(); });\n}\n\n// animation\n\n.form-item {\n  transition-property: color, border, background-color;\n  transition-duration: @time-giant;\n  transition-timing-function: @bez-out;\n  &:focus, &.active {\n    transition-property: color, border, background-color;\n    transition-duration: @time-giant;\n    transition-timing-function: @bez-in;\n  }\n}\n\n/**\n * select\n */\n\n.form-item {\n  select:not([multiple])& {\n    @select-arrow-color: @text-inverse;\n    @select-space-color: fade(@gray-800, 20%);\n    background-image: linear-gradient(45deg, transparent 50%, @select-arrow-color 50%), linear-gradient(135deg, @select-arrow-color 50%, transparent 50%), linear-gradient(to right, @select-space-color, @select-space-color);\n  }\n  select[multiple]& {\n    padding: 0;\n    option {\n      padding: .5rem 1rem;\n    }\n  }\n}\n\n/**\n * check and radio\n */\n\n// label\n\n.checkradio-label {\n  .margin(all, none); // reset .form-label\n  .padding(left, small); // space from checkbox/radio\n  .font-sans--regular();\n  font-size: 1em;\n  line-height: 1.2;\n  letter-spacing: 0;\n  text-transform: none;\n  // disabled\n  &.disabled {\n    opacity: 1;\n    &:before {\n      background: darken(@gray-200, 2.5%) !important;\n      border: 1px solid darken(@gray-200, 2.5%) !important;\n    }\n    &:after {\n      color: darken(@gray-200, 25%) !important;\n    }\n  }\n}\n\n// styled\n\n.checkbox, .radio {\n  padding-left: 3rem;\n  &:before, &:after {\n    width: 1.8rem;\n    height: 1.8rem;\n  }\n  &:before {\n    background: @gray-200;\n    border: 1px solid @gray-300;\n  }\n  &:after {\n    color: @accent;\n  }\n}\n\n.checkbox {\n  &:before {\n    border-radius: 4px;\n  }\n  &:after {\n    .icon-xt();\n    content: @icon-xt-check;\n    font-size: 100%;\n  }\n}\n\n.radio {\n  &:before {\n    border-radius: 50%;\n  }\n  &:after {\n    .icon-xt();\n    content: @icon-xt-circle;\n    font-size: 50%;\n  }\n}\n\n// pseudo\n\ninput[type=\"checkbox\"]:not(.unstyled) {\n  // focus\n  &:focus ~ .form-label {\n    .checkbox.focus();\n  }\n  // active\n  &:checked ~ .form-label {\n    .checkbox.active();\n  }\n  // disabled\n  &:disabled ~ .form-label {\n    .checkradio-label.disabled();\n  }\n}\n\ninput[type=\"radio\"]:not(.unstyled) {\n  // focus\n  &:focus ~ .form-label {\n    .radio.focus();\n  }\n  // active\n  &:checked ~ .form-label {\n    .radio.active();\n  }\n  // disabled\n  &:disabled ~ .form-label {\n    .checkradio-label.disabled();\n  }\n}\n\n// animation\n\n.checkbox, .radio {\n  &:before {\n    transform: scale(.9, .9);\n    transition-property: all;\n    transition-duration: @time-medium;\n    transition-timing-function: @bez-out;\n  }\n  &:after {\n    opacity: 0 !important;\n    transform: scale(.9, .5);\n    transition-property: all;\n    transition-duration: @time-medium;\n    transition-timing-function: @bez-out;\n  }\n  &.focus, &:focus {\n    &:before {\n      border: 1px solid @accent;\n    }\n  }\n  &.active {\n    &:before {\n      transform: scale(1, 1);\n      transition-property: all;\n      transition-duration: @time-medium;\n      transition-timing-function: @bez-in;\n    }\n    &:after {\n      opacity: 1 !important;\n      transform: scale(1, 1);\n      transition-property: all;\n      transition-duration: @time-medium;\n      transition-timing-function: @bez-in;\n    }\n  }\n}\n\n/**\n * form-note\n */\n\n.form-note {\n  .note();\n  .note--default();\n  .note--small();\n  .muted();\n}\n\n/**\n * validation\n */\n\n.form-item {\n  &:invalid {\n    box-shadow: none !important;\n  }\n}\n"