export default "@import '~xtend-library/src/core/list/_list.less';\n\n/**\n * vars\n */\n\n// generate class: [false], [true] non responsive only, [sm, md, lg] responsive @breakpoints\n\n@generate-list: true;\n\n// space\n\n@list-spaces: none, micro, tiny, small, medium, big, giant, huge; // if you change this change also variables names below\n@list-space--none: 0 0; // vertical horizontal\n@list-space--micro: .25rem .25rem;\n@list-space--tiny: .5rem .5rem;\n@list-space--small: .75rem .75rem;\n@list-space--medium: 1rem 1rem;\n@list-space--big: 1.5rem 1.5rem;\n@list-space--giant: 2rem 2rem;\n@list-space--huge: 3rem 3rem;\n\n/**\n * list\n */\n\n// variant\n\n.list--default {\n  // default options\n  .list-space(small);\n  .list-btns({\n    .btn();\n    .btn--default();\n  });\n}\n\n.list--drop {\n  flex-wrap: nowrap;\n  flex-direction: column;\n  // default options\n  .list-space(none);\n  .list-btns({\n    .btn();\n    .btn--small();\n    .btn--squared();\n    padding-left: 2.2rem;\n    padding-right: 2.2rem;\n    white-space: nowrap;\n  });\n}\n"