export default "@import '~xtend-library/src/core/badge/_badge.less';\n\n/**\n * badge\n */\n\n.badge {\n  border-radius: 0;\n  border-width: 1px;\n  border-style: solid;\n  border-color: transparent;\n  .font-sans--semibold();\n  line-height: 1.4;\n  letter-spacing: .025em;\n  text-transform: uppercase;\n  // default options\n  .badge--medium();\n}\n\n// variant\n\n.badge--default {\n  border-color: @gray-300;\n  background: @gray-300;\n  color: @text;\n}\n\n.badge--primary {\n  border-color: @accent;\n  background: @accent;\n  .text-inverse();\n}\n\n.badge--success {\n  border-color: @success;\n  background: @success;\n  .text-inverse();\n}\n\n.badge--error {\n  border-color: @error;\n  background: @error;\n  .text-inverse();\n}\n\n// size\n\n.badge--small {\n  font-size: 11px;\n  padding: .3rem .8rem;\n}\n\n.badge--medium {\n  font-size: 13px;\n  padding: .4rem .9rem;\n}\n\n.badge--big {\n  font-size: 15px;\n  padding: .5rem 1rem;\n}\n\n// special\n\n.badge--squared {\n  border-radius: 0;\n}\n"